











































import Vue from 'vue';
import NumberPlate from '@/components/NumberPlate.vue';
import Buefy from 'buefy'

Vue.use(Buefy);

export default Vue.extend({
    name: 'AddSpotting',
    props: {
        plate: {
            type: Number,
            default: 0
        },
        teamEffort: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            isOpen: false,
            date: new Date(),
            seenBy: [],
            note: ''
        }
    },
    components: {
        NumberPlate
    },
    methods: {
        save() {
            console.log('Date: ', this.date);
            console.log('Seen by: ', this.seenBy);
            console.log('Note: ', this.note);

            //if (teamEffort && this.seenBy.length)

            this.$emit('create', {
                plate: this.plate,
                date: this.date,
                seenBy: this.seenBy,
                note: this.note
            });
            this.isOpen = false;
        }
    }
});
